@keyframes rotate-circle {
	0% {
		transform: rotate(0deg);
	}
	100% {
		transform: rotate(1440deg);	
	}
}

@keyframes icons-section-height-1 {
	0% {
		height: 0;
	}
	100% {
		height: $icons-section-height-mobile;
	}
}

@keyframes icons-section-height-2 {
	0% {
		height: 0;
	}
	100% {
		height: $icons-section-height-tablet;
	}
}

@keyframes icons-section-height-3 {
	0% {
		height: 0;
	}
	100% {
		height: $icons-section-height-desktop;
	}
}

@keyframes scale-up {
	0% {
		transform: scale(0);
	}
	100% {
		transform: scale(1);
	}
}