.site-header {
	color: $color-white;
	left: 0;
	position: fixed;
	right: 0;
	top: 0;
	z-index: 100;
	
	@media (min-width:768px) {
		height: $header-height-normal;
		padding-top: 14px;
		// padding-bottom: 25px;
	}

	&:before {
		background: $gradient-green-lime;
		bottom: 0;
		content: '';
		display: block;
		left: 0;
		position: absolute;
		opacity: 0;
		right: 0;
		top: 0;
		transition: $transition-1;
		z-index: -1;
	}

	.container {
		align-items: center;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		
		@media (min-width:768px) {
			justify-content: flex-start;
		}

		.contact {
			align-items: center;
			display: flex;
			justify-content: flex-end;

			@media (min-width:768px) {
				flex-basis: 50%;
				margin-bottom: 1em;
				order: 1;
				text-align: right;
			}

			@media (min-width:992px) {
				flex-basis: 100%;
				margin-bottom: 0;
				padding-bottom: 30px;
			}

			a {
				align-items: center;
				display: flex;
				margin-left: 30px;

				i {
					display: block;
					margin-right: 13px;
					transform: translateY(-1px);
				}

				span {
					display: block;
					font-size: .77em;
				}
			}
		}

		.logo {
			display: block;

			@media (min-width:768px) {
				margin-right: 2em;
				order: 2;
			}

			@media (min-width:1300px) {
				margin-right: 4.05em;
			}

			img {
				display: block;
				height: 2.27em;
				width: auto;
			}
		}

		.navbar-toggle {
			@media (min-width:768px) {
				display: none;
			}
		}

		.main-nav-wrapper {
			@media (min-width:768px) {
				order: 3;
				margin-left: auto;
			}

			@media (min-width:992px) {
				margin-left: 0;
			}

			.menu {
				list-style: none;
				margin-bottom: 0;
				padding: 0;

				@media (min-width:768px) {
					align-items: center;
					display: flex;
				}

				@media (min-width:992px) {
					position: relative;
				}

				&-item {
					@media (min-width:768px) {
						margin-right: 1.8em;
					}

					@media (min-width:992px) {
						position: relative;
					}

					@media (min-width:1300px) {
						margin-right: 2.5em;
					}

					&:last-child {
						@media (min-width:768px) {
							margin-right: 0;
						}
					}

					a {
						transition: $transition-1;

						@media (min-width:768px) {
							font-size: 1.1em;
						}

						@media (min-width:992px) {
							font-size: 1.33em;
						}

						&[href*="http"],
						&[href*="#"] {
							@media (min-width:1200px) {
								&:hover {
									color: $color-primary;
								}
							}
						}
					}

					.sub-menu {
						&.top-level {
							display: none;

							@media (min-width:768px) {
								align-items: flex-start;
								// display: flex;
								left: 50%;
								list-style: none;
								margin: 0;
								min-width: 200px;
								padding: 0;
								position: absolute;
								transform: translateX(-50%);
							}

							@media (min-width:992px) {
								left: 0;
								transform: translateX(0);
							}

							> .menu-item {
								@media (min-width:768px) {
									padding-top: 1.5em;
								}

								@media (min-width:992px) {
									padding-top: .6em;
								}
								
								> a {
									font-weight: 700;
									line-height: 1.33;

									&[href*="http"],
									&[href*="#"] {
										font-weight: 300;
									}
								}
							}

							&.active {
								display: block;

								@media (min-width:768px) {
									display: flex;
								}
							}
						}

						&[class*="depth"] {
							list-style: none;
							margin: 0;
							padding: 0;

							.menu-item {
								a {
									line-height: 1.33;
									white-space: nowrap;
								}
							}
						}
					}
				}
			}
		}

		.social {
			align-items: center;
			display: flex;

			@media (min-width:768px) {
				flex-basis: 50%;
				margin-bottom: 1em;
				order: 0;
			}

			@media (min-width:992px) {
				flex-basis: auto;
				margin-left: auto;
				margin-bottom: 0;
				order: 4;
			}

			.icon {
				align-items: center;
				background: #fff;
				border-radius: 300px;
				color: $color-black;
				display: flex;
				font-size: 16px;
				height: 27px;
				justify-content: center;
				margin: 0 5px;
				transition: $transition-1;
				width: 27px;

				@media (min-width:768px) {
					margin: 0 0 0 10px;
				}

				&:hover {
					background: $color-primary;
				}
			}
		}
	}

	&.with-bg {
		&:before {
			opacity: 1;
		}

		.container {
			.main-nav-wrapper {
				.menu {
					&-item {
						a {
							&[href*="http"],
							&[href*="#"] {
								@media (min-width:1200px) {
									&:hover {
										color: $color-black;
									}
								}
							}
						}
					}
				}
			}

			.social {
				.btn {
					background: $color-white;
					color: $color-black;
					// border: 2px solid $color-white;
					// font-weight: 700;

					@media (min-width:1200px) {
						&:hover {
							background: $color-black;
							color: $color-white;
						}
					}
				}

				.icon {
					@media (min-width:1200px) {
						&:hover {
							background: $color-black;
							color: $color-white;
						}
					}
				}
			}
		}
	}

	&.on-green {
		.container {
			.social {
				.btn {
					background: $color-white;
					color: $color-black;
					// border: 2px solid $color-white;
					// font-weight: 700;

					@media (min-width:1200px) {
						&:hover {
							background: $color-black;
							color: $color-white;
						}
					}
				}

				.icon {
					@media (min-width:1200px) {
						&:hover {
							background: $color-black;
							color: $color-white;
						}
					}
				}
			}
		}
	}

	@media (max-width:1199px) {
		.container {
			.contact {
				flex-basis: 50%;
				padding-bottom: 0;
			}

			.social {
				flex-basis: 50%;
				order: 0;
				padding-bottom: 15px;
			}
		}
	}


	@media (max-width:767px) {
		.container {
			padding-top: .5em;
			padding-bottom: .5em;

			.logo {
				order: 0;

				img {
					height: 2em;
				}
			}

			.navbar-toggle {
				display: block;
				// flex-basis: 50%;
				order: 1;
				text-align: right;

				.inner {
					display: inline-block;
					height: 14px;
					position: relative;
					width: 24px;
					vertical-align: middle;
					transition: $transition-1;
				}

				.icon-bar {
					background: $color-white;
					height: 2px;
					left: 0;
					position: absolute;
					right: 0;

					&:first-child {
						top: 0;
					}

					&:nth-child(2) {
						top: 50%;
						transform: translateY(-50%);
					}

					&:last-child {
						bottom: 0;
					}
				}
			}

			.main-nav-wrapper {
				display: none;
				flex-basis: 100%;
				order: 2;
				padding: 1em 0;

				.menu {
					&-item {
						a {
							color: #b1b0b0;
							display: block;
							font-size: 1.25em;
							font-weight: 700;
							letter-spacing: .02em;
							padding: .8em 0;
						}

						.sub-menu {
							list-style: none;
							margin: 0;
							padding: 0;

							&.top-level {
								> .menu-item {
									@media (min-width:768px) {
										padding-top: .6em;
									}
									
									> a {
										color: $color-white;
										padding: 0;

										&[href*="http"],
										&[href*="#"] {
											color: #b1b0b0;
											padding: .8em 0;
										}
									}
								}
							}

							&[class*="depth"] {
								padding-left: 1em;

								.menu-item {
									a {
										font-size: 1em;
									}
								}
							}
						}
					}

					> .menu-item-has-children {
						> a {
							position: relative;

							&:after {
								color: $color-white;
								content: '\e90f';
								font-family: $font-icon;
								font-weight: 300;
								font-size: .8em;
								position: absolute;
								top: 50%;
								transform: translateX(5px) translateY(-50%) rotate(180deg);
							}
						}
					}

					&.sub-nav-active {
						> .menu-item {
							&:not(.active) {
								display: none;
							}

							&.active {
								> a {
									color: $color-primary;

									&:after {
										transform: translateX(5px) translateY(-50%) rotate(0deg);
									}
								}
							}
						}
					}
				}
			}

			.social {
				display: none;
				flex-basis: 100%;
				order: 3;
			}

			.contact {
				bottom: 1em;
				display: none;
				flex-basis: 100%;
				order: 4;
				position: absolute;

				a {
					margin-left: 0;
					margin-top: .5em;

					span {
						font-size: 1em;
					}
				}
			}
		}

		&.mobile-nav-active {
			background-color: rgba(#051c12, .8);

			&:before {
				display: none;
			}

			// &:after {
			// 	background-image: url('../../assets/images/mobile-nav-bg.jpg');
			// 	background-size: cover;
			// 	background-position: center top;
			// 	content: '';
			// 	opacity: .15;
			// }

			.container {
				.navbar-toggle {

					.inner {
						transform: rotate(90deg);
					}
				}

				.main-nav-wrapper {
					display: block;
				}

				.contact {
					display: block;
				}
			}
		}
	}
}