.page-banner {
	align-items: flex-end;
	color: $color-white;
	display: flex;
	overflow: hidden;
	position: relative;

	::-moz-selection {
		background-color: rgba($color-white, 0.99);
		color: $color-black;
	}
	
	::selection {
		background-color: rgba($color-white, 0.99);
		color: $color-black;
	}

	.container {
		align-items: center;
		display: flex;
		height: calc(100% - #{$header-height-short});
		justify-content: space-between;
		position: relative;
		z-index: 5;

		@media (min-width:768px) {
			height: calc(100% - #{$header-height-normal});
		}
	}

	&.default {
		background: $gradient-green-lime;
		height: $banner-default-height-mobile;

		@media (min-width:768px) {
			height: $banner-default-height-tablet;
		}

		@media (min-width:992px) {
			height: $banner-default-height-desktop;
		}

		.container {
			.page-title {
				font-size: 2.5em;
				line-height: 1.1;
				margin: 0;

				@media (min-width:411px) {
					font-size: 3em;
				}

				@media (min-width:768px) {
					flex-basis: 54%;
					font-size: 2.75em;
					line-height: .8;
				}

				@media (min-width:992px) {
					font-size: 4.2em;
				}

				@media (min-width:1200px) {
					font-size: 5.1em;
				}
			}

			.page-description {
				display: none;

				@media (min-width:768px) {
					display: block;
					flex-basis: 44%;
				}

				@media (min-width:992px) {
					flex-basis: 40%;
				}
			}
		}

		&.title-only {
			.container {
				.page-title {
					@media (min-width:768px) {
						flex-basis: 100%;
					}
				}
			}
		}
	}

	&.image {
		background-color: #051c12;
		height: auto;
		min-height: $banner-image-height-mobile;

		@media (min-width:768px) {
			height: $banner-image-height-tablet;
			min-height: 0;
		}

		@media (min-width:992px) {
			height: $banner-image-height-desktop;
		}

		&.overlay-green {
			background-color: #0c3b1b;
		}

		.container {
			flex-wrap: wrap;
			padding-top: 60px;
			padding-bottom: 30px;

			@media (min-width:768px) {
				padding-top: 0;
				padding-bottom: 0;
			}

			.text-top {
				color: #8dc879;
				font-size: 1.3em;
				flex-basis: 100%;
				font-weight: 700;
				margin-top: auto;
				text-transform: uppercase;

				@media (min-width:768px) {
					font-size: 1.25em;
				}

				@media (min-width:992px) {
					font-size: 1.5em;
				}

				@media (min-width:1500px) {
					font-size: 2.1em;
				}

				& ~ div {
					margin-bottom: auto;
				}
			}

			.page-title,
			.second-title {
				font-size: 2.2em;
				line-height: 1;

				@media (min-width:411px) {
					font-size: 3em;
				}

				@media (min-width:768px) {
					font-size: 2.625em;
				}

				@media (min-width:992px) {
					font-size: 3em;
				}

				@media (min-width:1200px) {
					font-size: 3.1em; //56
				}

				&.large {
					font-size: 2.5em;
					margin-bottom: .3em;

					@media (min-width:411px) {
						font-size: 3em;
					}

					@media (min-width:768px) {
						font-size: 2.25em;
					}

					@media (min-width:992px) {
						font-size: 3.7em;
						line-height: .9;
					}

					@media (min-width:1500px) {
						font-size: 5.1em;
					}					
				}
			}

			.page-description {
				font-size: 1em;
				line-height: 1.4;

				@media (min-width:768px) {
					font-size: 1em;
					line-height: 1.16;
				}

				@media (min-width:992px) {
					font-size: 1.25em;
				}

				@media (min-width:1500px) {
					font-size: 1.33em;
				}
			}

			&.columns-two {
				@media (max-width:767px) {
					.text-right {
						margin-top: 1em;
						text-align: right;
					}
				}

				&.split {
					&-halves {
						.text-left,
						.text-right {
							@media (min-width:768px) {
								flex-basis: 45%;
							}
						}
					}

					&-first_small {
						.text-left {
							@media (min-width:768px) {
								flex-basis: 38%;
							}
						}

						.text-right {
							@media (min-width:768px) {
								flex-basis: 58%;
							}
						}
					}

					&-first_large {
						.text-left {
							@media (min-width:768px) {
								flex-basis: 58%;
							}
						}

						.text-right {
							@media (min-width:768px) {
								flex-basis: 38%;
							}
						}
					}
				}
			}

			&.columns-single {
				.text-left {
					@media (min-width:768px) {
						align-items: center;
						display: flex;
						flex-basis: 90%;
						flex-wrap: wrap;
					}

					@media (min-width:992px) {
						flex-basis: 80%;
					}

					@media (min-width:1200px) {
						flex-basis: 75%;
					}

					.page-title {
						margin: 0;
					}

					.btn {
						margin-top: 1em;

						@media (min-width:768px) {
							margin-left: 1em;
							margin-top: 0;
						}

						@media (min-width:992px) {
							margin-left: 1.5em;
						}

						@media (min-width:1200px) {
							margin-left: 2em;
						}

						@media (max-width:500px) {
							font-size: 1em;
							height: 45px;
							min-width: 180px;
						}
					}

					.page-description {
						margin-top: 1em;

						@media (min-width:768px) {
							flex-basis: 100%;
						}
					}
				}
			}
		}

		.media {
			bottom: 0;
			left: 0;
			opacity: .45;
			overflow: hidden;
			position: absolute;
			right: 0;
			top: 0;

			> img {
				left: 50%;
				max-width: none;
				min-height: 101%;
				min-width: 101%;
				position: absolute;
				top: 50%;
				transform: translate(-50%, -50%);
				width: auto;
			}

			.image {
				background-size: cover;
				background-position: center center;
				bottom: 0;
				left: 0;
				position: absolute;
				right: 0;
				top: 0;
			}
		}

		&.jobs-banner {
			.container {
				.title {
					line-height: 1.05;	
					margin: 0;

					@media (min-width:768px) {
						font-size: 2.8em;
						flex-basis: 50%;
					}

					@media (min-width:992px) {
						font-size: 3.7em;
					}

					@media (min-width:1200px) {
						font-size: 3.8em;
					}

					@media (min-width:1300px) {
						font-size: 4em;
					}

					@media (min-width:1500px) {
						font-size: 5.11em;
					}

					span {
						color: $color-primary;
					}
				}

				.page-description {
					@media (min-width:768px) {
						flex-basis: 43%;
					}
				}
			}
		}
	}
}