.large-banner {
	height: 80vh;
	visibility: hidden;

	@media (min-width:768px) {
		height: 550px;	
	}

	@media (min-width:992px) {
		height: 600px;	
	}

	@media (min-width:1300px) {
		height: 938px;	
	}

	.slick-list,
	.slick-track {
		height: 100%;
	}

	&-slide {
		height: 100%;
		position: relative;

		.inner {
			align-items: flex-start;
			display: flex;
			height: 100%;
			padding-top: 80px;

			@media (min-width:768px) {
				align-items: center;
				padding-top: 30px;
			}

			.text-wrapper {
				color: $color-white;
				position: relative;
				z-index: 5;

				@media (min-width:768px) {
					align-items: center;
					display: flex;
					flex-wrap: wrap;
					justify-content: space-between;
				}

				.text-top {
					color: $color-primary;
					font-size: 1.5em;
					margin-bottom: .1em;

					@media (min-width:768px) {
						font-size: 2em;
						flex-basis: 100%;
					}

					@media (min-width:1200px) {
						font-size: 3.1em;
					}
				}

				.text-left {
					font-size: 3.5em;
					font-weight: 700;
					line-height: .8;
					margin-bottom: .5em;

					@media (min-width:768px) {
						flex-basis: 48%;
						font-size: 5.8em;
						margin-bottom: 0;
					}

					@media (min-width:992px) {
						font-size: 6em;
					}

					@media (min-width:1200px) {
						font-size: 9.6em;
					}
				}

				.text-right {
					padding-right: 15%;

					@media (min-width:768px) {
						flex-basis: 48%;
						font-size: .9em;
						padding-right: 0;
					}

					@media (min-width:992px) {
						font-size: 1.1em;
					}

					@media (min-width:1200px) {
						font-size: 1.33em;
					}
				}
			}

			.media {
				background: #051c12;
				bottom: 0;
				left: 0;
				overflow: hidden;
				position: absolute;
				right: 0;
				top: 0;

				> img {
					left: 50%;
					max-width: none;
					min-height: 101%;
					min-width: 101%;
					opacity: .35;
					position: absolute;
					top: 50%;
					transform: translate(-50%, -50%);
					width: auto;
				}

				.video-container {
					background: $color-black;
					top: 50%;
					transform: translateY(-50%);
					height: 100%;
					left: 0%;
					opacity: .35;
					overflow: hidden;
					position: relative;
					right: 0;

					video {
						min-width: 101%;
						min-height: 101%;
						left: 50%;
						top: 50%;
						transform: translateY(-50%) translateX(-50%);
						// opacity: 0;
						position: absolute;
						transition: $transition-1;
						width: auto;
						z-index: 1;

						&.fade-in {
							opacity: 1;
						}
					}

					.poster {
						img {
							top: 50%;
							transform: translateY(-50%);
							position: absolute;
							width: 100%;
						}
					}
				}
			}
		}
	}

	.slick-dots {
		bottom: 15px;
		left: 50%;
		position: absolute;
		transform: translateX(-50%);

		@media (min-width:768px) {
			bottom: 3.77em
		}
	}

	&.no-dots {
		.slick-dots {
			display: none;
		}
	}
}