.site-footer {
	background: $gradient-green-lime;
	border-top: 5px solid $color-white;
	color: $color-white;
	padding-top: 3.43em;
	padding-bottom: 2.8em;

	@media (min-width:992px) {
		padding-top: 5.83em;
		padding-bottom: 4.1em;
	}

	::-moz-selection {
		background-color: rgba($color-white, 0.99);
		color: $color-black;
	}
	
	::selection {
		background-color: rgba($color-white, 0.99);
		color: $color-black;
	}

	.container {
		align-items: flex-start;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		.title {
			font-size: 1.11em;
		}

		ul {
			list-style: none;
			margin: 0;
			padding: 0;

			li {
				margin-bottom: .72em;

				a {
					display: block;
					font-size: 1em;
					line-height: 1.11;
					transition: $transition-1;

					&[href*="http"],
					&[href*="#"] {
						&:hover {
							color: $color-black;
						}
					}
				}

				
			}
		}

		.recent-posts {
			display: none;

			@media (min-width:768px) {
				display: block;
				flex-basis: 24%;
			}

			ul {
				li {
					@media (min-width:992px) {
						margin-bottom: 1.83em;
					}
				}
			}
		}

		.menu {
			@media (min-width:411px) {
				flex-basis: 58%;
			}

			@media (min-width:768px) {
				flex-basis: 24%;
			}

			> .menu-item {
				margin-bottom: 41px;

				> a {
					display: block;
					font-size: 1.11em;
					font-weight: 700;
					line-height: 1.25;
					margin-bottom: .4em;
				}
			}
		}

		.contact {
			@media (min-width:411px) {
				flex-basis: 40%;
			}

			@media (min-width:768px) {
				flex-basis: 46%;
			}

			h3 {
				font-size: 1.11em;
			}

			p {
				line-height: 1.11;
				margin-bottom: .72em;

				a {
					transition: $transition-1;

					&:hover {
						color: $color-black;
					}
				}
			}

			.locations {
				@media (min-width:768px) {
					columns: 2;
				}

				&-single {
					margin-bottom: .83em;

					@media (min-width:768px) {
						-webkit-column-break-inside: avoid;
						page-break-inside: avoid;
						-webkit-column-break-inside:avoid; 
						-moz-column-break-inside:avoid; 
						-o-column-break-inside:avoid; 
						-ms-column-break-inside:avoid; 
						break-inside: avoid-column;
						column-break-inside:avoid;
						display: block;
					}

					.name {
						font-size: 1em;
						line-height: 1.11;
						margin: 0 0 4px;
					}

					.phone {
						line-height: 1.11;
						margin: 0;
					}

					.full-address {
						font-style: normal;
						line-height: 1.11;
						margin: 0;
					}
				}
			}
		}

		.logo {
			display: none;

			@media (min-width:768px) {
				display: block;
				flex-basis: 100%;
				padding-top: 1.9em;
			}

			a {
				display: inline-block;
			}

			img {
				display: block;
				height: 52px;
				width: auto;
			}
		}
	}
}