.btn {
	align-items: center;
	border-radius: .55em;
	color: $color-white;
	display: inline-flex;
	font-size: 1.25em;
	font-weight: 700;
	// min-width: 204px;
	min-width: 232px;
	height: 60px;
	justify-content: center;
	// padding: .9em .75em;
	padding: 5px;
	text-align: center;
	transition: $transition-1;
	width: auto;

	&.btn-xs {
		font-size: .77em;
		font-weight: 300;
		height: 28px;
		min-width: 160px;
	}

	&.btn-transparent {
		background-color: transparent;
		border: 2px solid $color-white;
		// padding-top: .8em;
		// padding-bottom: .8em;

		&:hover {
			background: $color-white;
			color: #57ad75;
		}
	}

	&.btn-gradient {
		background: $gradient-green-lime;
		background-size: 100% 100%;
		transition: $transition-1;

		&:hover {
			background-size: 250% 100%;		
			transition: all .5s ease-in;
		}

		&-top {
			background: $gradient-green-lime-vertical;

			&:hover {
				background-size: 100% 110%;		
			}
		}
	}

	&.btn-green {
		background:#57ad75;

		&:hover {
			background: $color-primary;
		}
	}
}