.section-content_only {
	.bg {
		padding-top: 2em;
		padding-bottom: 2em;

		@media (min-width:768px) {
			padding-top: 4.125em;
			padding-bottom: 3.75em;
		}

		@media (min-width:1200px) {
			padding-top: 4.8em;
			padding-bottom: 4.6em;
		}

		&.default {
			background-color: $color-white;
		}

		&.grey {
			background-color: #363636;
			color: $color-white;
		}

		&.green {
			background: $gradient-green-lime;
			color: $color-white;
		}
	}

	.container {
		.content {
			.column-title {
				font-size: 1.75em;
				letter-spacing: .02em;

				@media (min-width:992px) {
					font-size: 2.375em;
				}

				@media (min-width:1200px) {
					font-size: 2.44em;
				}
			}

			.text {
				line-height: 1.16;

				@media (min-width:992px) {
					font-size: 20px;
				}

				p {
					margin-bottom: .5em;
				}
			}

			.btn {
				margin-top: 1em;
			}
		}

		&.columns-single {
			.content {
				@media (min-width:768px) {
					margin: auto;
					width: 85%;
				}

				h2 {
					font-size: 1.8em;
					line-height: 1;

					@media (min-width:768px) {
						font-size: 2.6em;
					}

					@media (min-width:992px) {
						font-size: 3em;
						line-height: .89;
					}

					@media (min-width:1200px) {
						font-size: 3.8em;
					}

					@media (min-width:1300px) {
						font-size: 4.6em;
					}
				}
			}
		}

		&.columns-two {
			@media (min-width:768px) {
				align-items: flex-start;
				display: flex;
				justify-content: space-between;
			}

			.content {
				margin: 1em 0;

				@media (min-width:768px) {
					flex-basis: 47%;
					margin: 0;
					width: auto;
				}

				.text {
					@media (min-width:1200px) {
						font-size: 24px;
					}
				}
			}
		}
	}
}