.post-type-archive-blog,
.single-blog,
.single-news {
	.section-latest_news {
		background: $gradient-green-lime;
		border: none;
		padding-top: 0;

		.section-title {
			color: $color-white;
			letter-spacing: .08em;
			margin: 0;
			text-transform: uppercase;
		}

		.slick-dots {
			li {
				button {
					&:before {
						border-color: $color-white;
					}

					&:after {
						background: $color-white;
					}
				}
			}
		}
	}

	.blog-wrapper {
		align-items: flex-start;
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;

		.top {
			align-items: center;
			display: flex;
			flex-basis: 100%;
			flex-wrap: wrap;
			justify-content: space-between;
			margin-bottom: 10px;
			order: 0;
			padding-top: 1.875em;

			@media (min-width:768px) {
				margin-bottom: 20px;
				padding-top: 2.8em;
			}

			@media (min-width:992px) {
				padding-top: 3.75em;
			}

			@media (min-width:1200px) {
				padding-top: 4.8em;
			}

			.title {
				color: #57ad75;
				font-size: 32px;
				font-weight: 700;
				letter-spacing: .08em;
				text-transform: uppercase;
			}

			.articles-search {
				flex-basis: 100%;

				@media (min-width:768px) {
					flex-basis: 50%;
					min-width: 0;
				}

				form {
					background-color: $color-white;
					align-items: stretch;
					display: flex;
					position: relative;

					&:before {
						color: #949494;
						content: '\f002';
						font-family: 'Font Awesome 5 Pro';
						font-size: 22px;
						font-weight: 300;
						left: 16px;
						position: absolute;
						top: 50%;
						transform: translateY(-50%);
					}

					input {
						background: none;
						border: 2px solid #58ae75;
						border-radius: .61em 0 0 .61em;
						color: $color-black;
						flex-shrink: 0;
						flex-basis: 60%;
						font-size: 16px;
						min-width: 0;
						padding-left: 50px;

						@media (min-width:768px) {
							flex-basis: 80%;
							font-size: 20px;
						}

						&::-webkit-input-placeholder {
							color: #949494;
						}
						
						&::-moz-placeholder {
							color: #949494;
						}
						
						&:-ms-input-placeholder {
							color: #949494;
						}
						
						&:-moz-placeholder {
							color: #949494;
						}
					}

					button {
						border-top-left-radius: 0;
						border-bottom-left-radius: 0;
						border: none;
						display: block;
						flex-shrink: 0;
						flex-basis: 40%;
						font-size: 20px;
						font-weight: 300;
						height: 44px;
						min-width: 0;

						@media (min-width:768px) {
							flex-basis: 20%;
						}
					}
				}
			}
		}

		.sidebar {
			flex-basis: 100%;
			letter-spacing: .02em;
			min-width: 0;
			order: 3;

			@media (min-width:768px) {
				flex-basis: 22.5%;
				order: 1;
			}

			@media (min-width:1500px) {
				flex-basis: 17.5%;
			}

			.filter-title {
				color: #6bb872;

				@media (min-width:1200px) {
					font-size: 1.55em;
				}
			}

			.filter {
				margin-bottom: 1em;

				@media (min-width:768px) {
					margin-bottom: 2em;
				}

				@media (min-width:992px) {
					margin-bottom: 2.5em;
				}

				&-toggle {
					background: none;
					border: none;
					color: #393939;
					font-size: 1.33em;
					margin-bottom: .5em;
					padding: 0 0 0 35px;
					position: relative;

					&:before {
						// background-image: url(data:image/png;base64,);
						// background-size: 100% 100%;
						// background-position: center center;
						color: #57ad75;
						content: '\e90f';
						font-family: $font-icon;
						left: 0;
						line-height: 1;
						position: absolute;
						top: 50%;
						transform: translateY(-50%);
					}
				}

				&-list {
					display: block;
					list-style: none;
					margin: 0;
					padding: 0;

					li {
						display: block;
						margin-bottom: 1em;

						a {
							align-items: flex-start;
							display: flex;

							@media (min-width:992px) {
								font-size: 1.25em;
							}

							@media (min-width:1500px) {
								font-size: 1.33em;
							}

							&:before {
								background: $color-white;
								border: 2px solid #6bb872;
								border-radius: 100px;
								content: '';
								display: block;
								flex-shrink: 0;
								height: 28px;
								margin-right: 10px;
								transition: $transition-1;
								width: 28px;

								@media (min-width:768px) {
									height: 22px;
									width: 22px;
								}

								@media (min-width:1200px) {
									height: 28px;
									width: 28px;
								}
							}

							&.selected {
								&:before {
									background: #6bb872;
								}
							}
						}
					}
				}

				&.closed {
					.filter-toggle {
						&:before {
							transform: translateY(-50%) rotate(180deg);
						}
					}

					.filter-list {
						display: none;
					}
				}
			}
		}

		.main {
			flex-basis: 100%;
			min-width: 0;
			order: 1;

			@media (min-width:768px) {
				flex-basis: 75%;
				order: 2;
			}

			@media (min-width:1500px) {
				flex-basis: 81%;
			}

			article {
				padding-bottom: 30px;
				width: 100%;

				& + article {
					padding-top: 30px;
				}

				.post-main {
					background: $color-white;
					box-shadow: -4px 4px 15px 3px rgba($color-black, .19);
					border-radius: 10px;
					padding: 15px 0;

					@media (min-width:992px) {
						padding: 30px 0;
					}

					.inner {
						align-items: flex-start;
						display: flex;
						flex-wrap: wrap;
						justify-content: space-between;
						position: relative;
						margin: 0 18px;

						@media (min-width:992px) {
							margin: 0 36px;
						}

						.date {
							background: $color-white;
							border-radius: 0 0 10px 0;
							color: #7abf70;
							display: inline-block;
							font-size: 1.05em;
							font-weight: 700;
							left: 0;
							padding: 7px 18px 12px 8px;
							position: absolute;
							text-align: center;
							text-transform: uppercase;
							top: 0;
							z-index: 10;

							@media (min-width:768px) {
								font-size: .8em;
								padding: 7px 15px 10px 6px;
							}

							@media (min-width:992px) {
								font-size: 1.05em;
								padding: 7px 18px 12px 8px;
							}
						}

						.image {
							background-color: $color-grey-light;
							flex-basis: 100%;
							margin-bottom: 1em;
							position: relative;
							overflow: hidden;
							z-index: 1;

							@media (min-width:768px) {
								flex-basis: 26.3%;
								margin-bottom: 0;
							}

							&:after {
								content: '';
								display: table;
								padding-top: 55%;

								@media (min-width:768px) {
									padding-top: 161%;
								}
							}

							img {
								left: 50%;
								max-width: 101%;
								min-width: 101%;
								min-height: 101%;
								position: absolute;
								transform: translate(-50%, -50%);
								top: 50%;
							}
						}

						.content {
							flex-basis: 100%;
							padding-bottom: 10px;

							@media (min-width:768px) {
								flex-basis: 71%;
							}
							
							@media (min-width:992px) {
								padding-top: .9em;
							}

							.author {
								color: #6bb872;
								display: block;
								font-size: 1em;
								font-weight: 700;
								margin-bottom: 5px;

								@media (min-width:768px) {
									font-size: 20px;
									margin-bottom: 15px;
								}

								@media (min-width:992px) {
									margin-bottom: 20px;
								}

								@media (min-width:1200px) {
									margin-bottom: 22px;
								}
							}

							.post-title {
								font-size: 20px;
								margin-bottom: 16px;
								text-transform: uppercase;
							}

							.post-text,
							.post-content {
								font-size: 1em;
								line-height: 1.2;

								@media (min-width:768px) {
									font-size: 16px;
								}

								@media (min-width:992px) {
									font-size: 18px;
								}

								@media (min-width:1500px) {
									font-size: 20px;
								}

								&.absolute {
									position: absolute;
								}

								a {
									color: $color-grey-dark;
									text-decoration: underline;
									transition: $transition-1;

									&:hover {
										color: #6bb872;
									}
								}

								h1 {
									font-size: 1.875em;
									line-height: 1.11;
								}

								h2, {
									font-size: 1.75em;
								}

								h3 {
									font-size: 1.675em;
								}

								h4 {
									font-size: 1.5em;
								}

								h5 {
									font-size: 1.33em;
								}

								h6 {
									font-size: 1.25em;
								}
							}

							.post-text {
								img {
									display: none;
								}
							}
						}

						.bottom {
							align-items: center;
							display: flex;
							flex-basis: 100%;
							flex-wrap: wrap;
							justify-content: space-between;
							padding-top: 1em;

							@media (min-width:768px) {
								flex-wrap: nowrap;
								justify-content: center;
							}

							@media (min-width:992px) {
								justify-content: flex-end;
								padding-top: 0;
							}

							.add-comment-toggle {
								background: none;
								border: none;
								color: #67b573;
								padding: 0;
								position: relative;

								&:after {
									content: '+';
									margin-left: 5px;
								}

								&.active {
									&:after {
										content: '-';
									}
								}
							}

							.comments-toggle {
								background: none;
								border: none;
								color: #67b573;
								padding: 0;
								position: relative;

								@media (min-width:768px) {
									margin-left: 20px;
								}
							}

							.sharing {
								align-items: center;
								color: #67b573;
								display: flex;
								flex-basis: 100%;
								justify-content: center;
								padding-top: .5em;
								position: relative;

								@media (min-width:768px) {
									flex-basis: auto;
									margin-left: 20px;
									padding-top: 0;
								}

								a {
									align-items: center;
									background: #67b573;
									border-radius: 100px;
									color: $color-white;
									display: flex;
									height: 26px;
									justify-content: center;
									margin: 0 5px;
									width: 26px;

									@media (min-width:992px) {
										height: 31px;
										width: 31px;
									}
								}
							}

							@media (min-width:768px) {
								> *:not(:first-child) {
									&:before {
										background: $color-grey-dark;
										content: '';
										height: 24px;
										left: -10px;
										position: absolute;
										top: 50%;
										transform: translateY(-50%);
										width: 1px;
									}
								}
							}
						}

						.read-more {
							color: #6bb872;
							font-size: 20px;

							@media (min-width:768px) {
								font-size: 16px;
							}

							@media (min-width:992px) {
								font-size: 20px;
							}
						}
					}
				}

				.post-comments {
					background: $color-white;
					box-shadow: -4px 4px 15px 3px rgba($color-black, .19);
					border-radius: 10px;
					display: none;
					margin-top: 10px;
					padding: 15px 0;

					@media (min-width:992px) {
						padding: 30px 0;
					}

					.inner {
						margin: 0 18px;

						@media (min-width:992px) {
							margin: 0 36px;
						}

						.title {
							color: #67b573;
							font-size: 1em;
							font-weight: 300;
							margin-bottom: .875em;
						}

						.single-comment {
							.meta {
								.author {
									color: #67b573;
									display: inline-block;
									font-weight: 700;
									font-size: 1.11em;
									margin-right: 5px;
									text-transform: uppercase;
								}

								.date {
									color: #8c8c8c;
									display: inline-block;
									font-size: 1em;
								}
							}

							.content {
								font-size: 1em;
								line-height: 1.33;
							}

							& + .single-comment {
								&:before {
									background: $color-grey-light;
									display: block;
									content: '';
									height: 1px;
									margin-top: 1em;
									margin-bottom: 1em;
									width: 100px;
								}
							}
						}
					}
				}

				.post-add-comment {
					background: $color-white;
					box-shadow: -4px 4px 15px 3px rgba($color-black, .19);
					border-radius: 10px;
					display: none;
					margin-top: 10px;
					padding: 15px 0;

					@media (min-width:992px) {
						padding: 30px 0;
					}

					.inner {
						margin: 0 18px;

						@media (min-width:992px) {
							margin: 0 36px;
						}

						form {
							@media (min-width:768px) {
								align-items: flex-end;
								display: flex;
								justify-content: space-between;
							}

							.inputs {
								align-items: flex-start;
								display: flex;
								flex-wrap: wrap;
								justify-content: space-between;

								@media (min-width:768px) {
									flex-basis: 65%;
								}

								.input-wrap {
									flex-basis: 46%;
									margin-bottom: 1em;

									&:last-child {
										margin-bottom: 0;
									}

									&.large {
										flex-basis: 100%;
									}

									input {
										background: #ebebeb;
										border: 1px solid transparent;
										border-radius: 8px;
										color: $color-black;
										font-size: 1em;
										font-weight: 700;
										height: 43px;
										padding: 5px 12px;
										width: 100%;

										&::-webkit-input-placeholder {
											color: #848484
										}
										
										&::-moz-placeholder {
											color: #848484
										}
										
										&:-ms-input-placeholder {
											color: #848484
										}
										
										&:-moz-placeholder {
											color: #848484
										}
									}

									textarea {
										background: $color-white;
										border: 1px solid transparent;
										border-radius: 8px;
										color: $color-black;
										font-size: 1em;
										font-weight: 700;
										min-height: 140px;
										padding: 10px 0;
										resize: none;
										width: 100%;

										&::-webkit-input-placeholder {
											color: #848484
										}
										
										&::-moz-placeholder {
											color: #848484
										}
										
										&:-ms-input-placeholder {
											color: #848484
										}
										
										&:-moz-placeholder {
											color: #848484
										}
									}
								}
							}

							.btn {
								height: 40px;
								width: 100%;

								@media (min-width:768px) {
									flex-basis: 20%;
									height: 45px;
									min-width: 0;
									width: auto;
								}

								@media (min-width:992px) {
									height: 55px;
								}

								@media (min-width:1200px) {
									height: 60px;
								}
							}
						}
					}
				}
			}
		}

		.load-more-wrapper {
			flex-basis: 100%;
			padding-top: 30px;
			padding-bottom: 50px;
			order: 2;
			text-align: center;

			@media (min-width:768px) {
				margin-left: auto;
				flex-basis: 81%;
				order: 3;
				padding-bottom: 100px;
				padding-top: 50px;
			}

			.btn {
				cursor: pointer;
			}
		}
	}
}

.single-blog,
.single-news {
	.blog-wrapper {
		padding-top: 1em;

		@media (min-width:768px) {
			padding-top: 2em;
		}

		.main {
			margin-left: auto;
			margin-right: auto;
		}
	}
}