.post-type-archive-job {
	.jobs-top {
		background: $gradient-green-lime;
		color: $color-white;
		position: relative;
		z-index: 10;

		.container {
			position: relative;

			@media (min-width:992px) {
				padding-top: 30px;
			}
		}

		.main-title {
			font-size: 36px;
			letter-spacing: .02em;
			line-height: 1.05;
			margin: 0;
			padding-top: 10px;
			text-align: center;

			@media (min-width:768px) {
				font-size: 42px;
				padding-top: 20px;
			}

			@media (min-width:992px) {
				background: $gradient-green-lime;
				background-size: 110%;
				background-position: 50% 0;
				border-radius: 11px 11px 0 0;
				font-size: 48px;
				left: 10px;
				padding-top: 25px;
				position: absolute;
				right: 10px;
				top: -60px;;
			}

			@media (min-width:1200px) {
				background-size: 130%;
			}

			@media (min-width:1300px) {
				font-size: 64px;
				top: -71px;;
			}

			@media (min-width:1500px) {
				background-size: 160%;
				background-position: 50% 0;
			}

			@media (min-width:1600px) {
				background-size: 200%;
			}
		}

		.intro {
			font-size: 1.25em;
			margin-bottom: 0;
			text-align: center;

			@media (min-width:768px) {
				font-size: 1.375em;
			}

			@media (min-width:992px) {
				font-size: 1.5em;
			}

			@media (min-width:1200px) {
				font-size: 1.55em;
			}
		}

		.filters {
			padding-top: 1em;
			padding-bottom: 1em;

			.filter {
				& + .filter {
					margin-top: 1em;

					@media (min-width:992px) {
						margin-top: 2em;
					}
				}

				&-selected {
					align-items: center;
					background: none;
					border: 2px solid $color-white;
					border-radius: 10px;
					display: flex;
					height: 58px;
					justify-content: space-between;
					padding: 5px 1.16em;
					width: 100%;

					.text {
						font-size: 1.55em;
						font-weight: 700;
						line-height: 1;
					}

					i {
						font-size: 1.25em;
					}
				}

				&-list {
					list-style: none;
					margin: 0;
					padding: 0;

					li {
						margin-top: 6px;

						.option {
							align-items: center;
							background: none;
							border: 2px solid $color-white;
							border-radius: 10px;
							display: flex;
							font-size: 1.55em;
							height: 58px;
							line-height: 1;
							justify-content: space-between;
							padding: 5px .71em;
							transition: $transition-1;
							width: 100%;

							&:hover {
								background: $color-white;
								color: #57ad75;
							}
						}
					}
				}

				&.closed {
					.filter-selected {
						i {
							transform: rotate(180deg);
						}
					}

					.filter-list {
						display: none;
					}
				}
			}
		}
	}

	.jobs-list {
		background-image: url('../../assets/images/jobs-bg.jpg');
		background-size: cover;
		background-repeat: no-repeat;
		background-position: top center;
		padding: 2em 0;

		@media (min-width:992px) {
			padding: 3em 0;
		}

		@media (min-width:1200px) {
			padding: 4em 0;
		}

		article {
			background: $color-white;
			border-radius: 10px;
			box-shadow: -4px 4px 15px 3px rgba($color-black, .19);
			margin-bottom: 2em;
			padding: 11px 0 33px;

			.job-inner {
				padding: 0 14px;

				.meta {
					background: $gradient-green-lime;
					border-radius: 8px;
					color: $color-white;
					display: block;
					font-weight: 700;
					font-size: 20px;
					letter-spacing: .02em;
					line-height: 1;
					padding: 10px 18px;
					width: 25%;
				}

				.content {
					padding-top: 20px;

					.job-name {
						font-size: 1.25em;

						@media (min-width:1200px) {
							font-size: 1.11em;
						}
					}

					.dates {
						margin-bottom: .5em;

						@media (min-width:768px) {
							align-items: center;
							display: flex;

							p {
								margin: 0;

								&:not(:first-child):before {
									content: '|';
									margin: 0 .5em;
								}
							}
						}
					}

					.salary {

					}

					.description {
						padding: 1em 0 0;
					}

					.read-more {
						color: #57ad75;
						display: inline-block;
						font-weight: 700;
						margin-bottom: 1em;
					}

					.apply {
						p {
							color: #57ad75;
							font-weight: 700;
						}

						.btn {
							font-size: 1em;
							height: 42px;
							min-width: 147px;
						}
					}
				}
			}
		}
	}
}