.section-text_large_image {
	align-items: center;
	background: #282828;
	display: flex;
	color: $color-white;
	min-height: 550px;
	position: relative;

	@media (min-width:768px) {
		height: 600px;
		min-height: 0;
	}

	@media (min-width:992px) {
		height: 700px;
	}

	@media (min-width:1200px) {
		height: 820px;
	}

	.container {
		.content {
			position: relative;
			text-align: right;
			z-index: 5;

			@media (min-width:768px) {
				margin-left: auto;
				width: 49%;
			}

			h2 {
				font-size: 38px;
				line-height: 1.08;

				@media (min-width:768px) {
					font-size: 48px;
				}

				@media (min-width:992px) {
					font-size: 56px;
				}

				@media (min-width:1200px) {
					font-size: 82px;
				}

				@media (max-width:767px) {
					br {
						display: none;
					}
				}
			}

			h3 {
				line-height: 1.08;
				font-size: 38px;

				@media (min-width:768px) {
					font-size: 38px;
				}

				@media (min-width:992px) {
					font-size: 52px;
				}

				@media (min-width:1200px) {
					font-size: 60px;
					line-height: 1.06;
				}

				@media (max-width:767px) {
					br {
						display: none;
					}
				}
			}

			.text {
				font-size: 20px;
				line-height: 1.14;
				margin-bottom: 1em;

				@media (min-width:768px) {
					margin-bottom: .8em;
				}

				@media (min-width:992px) {
					font-size: 24px;
				}

				@media (min-width:1200px) {
					font-size: 28px;
				}
			}
		}

		.image {
			background-size: cover;
			background-position: center center;
			bottom: 0;
			right: 0;
			left: 0;
			opacity: .2;
			position: absolute;
			top: 0;
			z-index: 0;

			@media (min-width:768px) {
				opacity: 1;
				right: 55%;
			}
		}

		&.alt {
			.content {
				text-align: left;

				@media (min-width:768px) {
					margin-left: 0;
				}
			}

			.image {
				@media (min-width:768px) {
					left: 55%;
					right: 0;
				}
			}
		}
	}

	
}