.section-green_bar_2_col {
	background: $gradient-green-lime;
	color: $color-white;
	padding-top: 3em;
	padding-bottom: 3em;

	@media (min-width:768px) {
		padding-top: 3.6em;
		padding-bottom: 2.77em;
	}

	.container {
		@media (min-width:768px) {
			align-items: center;
			display: flex;
			justify-content: space-between;
		}

		::-moz-selection {
			background-color: rgba($color-white, 0.99);
			color: $color-black;
		}
		
		::selection {
			background-color: rgba($color-white, 0.99);
			color: $color-black;
		}

		.title {
			font-size: 3em;
			line-height: .85;
			margin: 0 0 .5em;

			@media (min-width:768px) {
				font-size: 3.8em;
				flex-basis: 48%;
				margin-bottom: 0;
			}

			@media (min-width:992px) {
				font-size: 4.5em;
			}

			@media (min-width:1200px) {
				font-size: 5.22em;
			}

			@media (max-width:767px) {
				br {
					display: none;
				}
			}
		}

		.content {
			@media (min-width:768px) {
				flex-basis: 49%;
			}

			.subtitle {
				font-size: 1.5em;
				line-height: .86;

				@media (min-width:992px) {
					font-size: 2.44em;
				}
			}

			.text {
				font-size: 1.25em;
				line-height: 1.33;
				margin-bottom: 1.5em;

				@media (min-width:768px) {
					margin-bottom: .8em;
				}

				@media (min-width:992px) {
					font-size: 1.33em;
				}
			}

			.btn {

			}
		}

		&.alt {
			text-align: right;

			@media (min-width:768px) {
				.title {
					order: 1;
				}

				.content {
					order: 0;
				}
			}
		}
	}
}