.contact-map {
	background-color: #0c2012;
	padding-top: 80px;
	padding-bottom: 50px;
	position: relative;

	@media (min-width:768px) {
		padding-top: 130px;
		padding-bottom: 100px;
	}

	@media (min-width:992px) {
		padding-top: 150px;
		padding-bottom: 150px;
	}

	@media (min-width:1200px) {
		padding-top: 190px;
		padding-bottom: 190px;
	}

	.container {
		position: relative;
		z-index: 5;
	}

	.image {
		background-image: url('../../assets/images/map-bg.jpg');
		background-size: cover;
		background-position: center center;
		bottom: 0;
		left: 0;
		opacity: .22;
		position: absolute;
		right: 0;
		top: 0;
		z-index: 1;
	}

	.page-title {
		color: $color-white;
		font-size: 2em;
		letter-spacing: .02em;

		@media (min-width:768px) {
			font-size: 2.5em;
		}

		@media (min-width:992px) {
			font-size: 3em;
		}

		@media (min-width:1300px) {
			font-size: 3.22em;
		}
	}

	.map-wrapper {
		align-items: stretch;
		background-color: $color-black;
		display: flex;
		flex-wrap: wrap;

		@media (min-width:768px) {
			flex-wrap: nowrap;
		}

		.content {
			background-color: $color-white;
			flex-basis: 100%;
			min-width: 0;
			padding: 30px;

			@media (min-width:768px) {
				flex-basis: 41%;
				padding: 30px;
			}

			@media (min-width:992px) {
				padding: 63px 90px 63px 50px;
			}

			@media (min-width:1200px) {
				padding: 63px 120px 63px 50px;
			}

			.title {
				background: $gradient-green-lime;
				-webkit-background-clip: text;
				background-clip: text;
				-webkit-text-fill-color: transparent;
				text-fill-color: transparent;

				@media (min-width:768px) {
					font-size: 2em;
					line-height: 1;
				}

				@media (min-width:992px) {
					font-size: 3em;
				}

				@media (min-width:1300px) {
					font-size: 3.5em;
				}
			}

			.text {
				font-size: 1em;
				line-height: 1.4;
				margin-bottom: 1em;

				@media (min-width:1200px) {
					font-size: 1.11em;
				}

				@media (min-width:1500px) {
					font-size: 1.25em;
				}
			}

			.map-explore {
				font-weight: 700;
				font-size: 20px;
				text-transform: uppercase;
			}
		}

		.gmap {
			flex-basis: 100%;
			min-width: 0;
			overflow: hidden;
			position: relative;

			&:after {
				content: '';
				display: table;
				padding-top: 75%;
			}

			@media (min-width:768px) {
				flex-basis: 59%;

				&:after {
					padding-top: 95%;
				}
			}

			@media (min-width:992px) {
				&:after {
					padding-top: 102%;
				}
			}

